/* Fonts */
@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Regular.ttf");
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Regular.ttf");
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Bold.ttf");
  font-weight: bold;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  font-family: "RobotoCondensed";
  font-size: 16px;
  line-height: 1.5em;
}

a {
  text-decoration: none;
  color: inherit;
}

.mobile nav {
  background-color: #ce181f;
  display: grid;
  grid-template-columns: 5fr auto auto;
  height: 48px;
  position: fixed;
  top: 0;
  width: 100%;
}

.mobile nav img {
  -ms-flex-item-align: center;
  align-self: center;
  height: 36px;
  margin-left: 4px;
}

@media only screen and (min-width: 980px) {
  .mobile nav img {
    display: none;
  }
}

.mobile nav .active {
  background-color: #e6e7e8 !important;
}

.mobile nav .active i,
.mobile nav .active p {
  color: #2F2F2F !important;
}

.mobile nav .mobile-button {
  background-color: #2F2F2F;
  cursor: pointer;
  display: grid;
  padding: 6px 0;
}

@media only screen and (min-width: 980px) {
  .mobile nav .mobile-button {
    display: none;
  }
}

.mobile nav .mobile-button .mobile-navigation {
  color: #fff;
  display: grid;
  justify-items: center;
  grid-gap: 10px;
  padding-left: 10px;
}

.mobile nav .mobile-button .mobile-navigation p {
  font-size: 12px;
  line-height: 0;
  padding-bottom: 5px;
  text-transform: uppercase;
  margin: 0;
}

.mobile nav .mobile-button .mobile-navigation i {
  font-size: 24px;
  padding-top: 2px;
}

@media only screen and (min-width: 980px) {
  .mobile nav .mobile-button .mobile-navigation {
    display: none;
  }
}

.mobile nav .navigation {
  background-color: #e6e7e8;
  position: fixed;
  right: 0;
  top: 48px;
  height: calc(100% - 47px);
  margin-bottom: 150px;
  overflow-y: auto;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
  width: 0;
  z-index: 1000;
}

@media only screen and (min-width: 980px) {
  .mobile nav .navigation {
    background-color: inherit;
    font-size: 1.1em;
    height: auto;
    letter-spacing: 1.5px;
    min-height: 70px;
    position: relative;
    text-align: center;
    top: 0;
    width: 100%;
  }
}

.mobile nav .navigation .container {
  border-bottom: 3px solid #8C8C8C;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
  text-transform: uppercase;
}

.mobile nav .navigation .container a {
  color: #4c4c4c;
  letter-spacing: 1px;
  line-height: 18px;
  padding: 10px;
}

.mobile nav .navigation .container a:not(:nth-child(6)) {
  border-bottom: 1px solid #B6B6B6;
}

.mobile nav .navigation .container .desktop-only {
  display: none;
}

@media only screen and (min-width: 980px) {
  .mobile nav .navigation .container {
    border-bottom: 0;
    grid-template-columns: repeat(7, 1fr);
  }
  .mobile nav .navigation .container a {
    border-bottom: 0 !important;
    color: #fff;
  }
  .mobile nav .navigation .container .desktop-only {
    display: block;
  }
}

.mobile nav .navigation .sub-navigation {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
}

.mobile nav .navigation .sub-navigation a {
  color: #4c4c4c;
  line-height: 18px;
  padding: 10px;
}

.mobile nav .navigation .sub-navigation a:not(:nth-child(7)) {
  border-bottom: 1px solid #B6B6B6;
}

@media only screen and (min-width: 980px) {
  .mobile nav .navigation .sub-navigation {
    display: none;
  }
}

.mobile nav .search-navigation {
  background-color: #e6e7e8;
  position: fixed;
  right: 0;
  top: 48px;
  height: calc(100% - 47px);
  margin-bottom: 150px;
  overflow-y: auto;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
  width: 0;
  z-index: 1000;
}

.mobile nav .search-navigation .search-box {
  border-bottom: 3px solid #8C8C8C;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
  text-transform: uppercase;
}

.mobile nav .search-navigation .search-box a {
  color: #4c4c4c;
  letter-spacing: 1px;
  line-height: 18px;
  padding: 10px;
}

.mobile nav .search-navigation .search-items {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 10px;
}

.mobile nav .search-navigation .search-items a {
  color: #4c4c4c;
  line-height: 18px;
  padding: 10px;
}

.mobile nav .search-navigation .search-items a:not(:nth-child(3)) {
  border-bottom: 1px solid #B6B6B6;
}

@media only screen and (min-width: 768px) {
  .navigation {
    background-color: #ce181f;
  }
  .navigation .container {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(7, 1fr);
    min-height: 70px;
    margin: auto;
    width: 95%;
    max-width: 1170px;
  }
  .navigation .container a {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
    font-size: 1.1em;
    letter-spacing: 1.5px;
    line-height: 1.1em;
    margin: 0 5px;
    text-align: center;
    text-transform: uppercase;
  }
  .navigation .container .nav-condense {
    font-size: .95em;
    letter-spacing: -1px;
  }
}

@media only screen and (min-width: 768px) and (min-width: 980px) {
  .navigation .container .nav-condense {
    letter-spacing: inherit;
    font-size: 1.1em;
  }
}

@media only screen and (min-width: 768px) and (min-width: 1200px) {
  .navigation .container .container {
    width: 80%;
  }
}

@media only screen and (min-width: 768px) {
  .sub-nav {
    background-color: #e6e7e8;
  }
  .sub-nav .sub-nav-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: auto;
    width: 95%;
    max-width: 1170px;
  }
  .sub-nav a {
    color: #333;
    font-size: .9em;
    line-height: 2rem;
    letter-spacing: .4px;
    padding-right: 6px;
    margin-right: 4px;
  }
  .sub-nav a:hover {
    color: #595959;
  }
  .sub-nav .first a:not(:nth-child(3)) {
    border-right: 1px solid #333;
  }
  .sub-nav .second {
    justify-self: end;
  }
  .sub-nav .second a:not(:nth-child(5)) {
    border-right: 1px solid #333;
  }
}

.border-right {
  border-right: 1px solid #777777;
  padding-right: 10px;
}

.padding-right {
  padding-right: 5px;
}

@media only screen and (max-width: 767px) {
  .desktop-only {
    display: none;
  }
}
